/** @format */

.flex-center {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	min-height: 100vh;
}

.overlay-loading {
	position: absolute;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgba(255, 255, 255, 0.75);
	z-index: 999;
}

.tactic-hint {
	padding-left: 16px;
	padding-right: 16px;
	padding-top: 8px;
	padding-bottom: 8px;
	background-color: black;
	color: white;
	border-radius: 5px;
	margin: 12px;
}

.tactic-hint-success {
	background-color: green;
}

.tactic-hint-error {
	background-color: red;
}
